; se encuentra equipado de tecnología, de material biológico como tejidos vegetales, y del equipo humano capacitado en diferentes especialidades, para desarrollar cada una de las actividades. En el laboratorio de biotecnología vegetal, se desarrollan técnicas experimentales en el cultivo de tejidos vegetales y se utilizan el mejoramiento genético y las herramientas, para la multiplicación o propagación de plantas in vitro, teniendo en cuenta todos los protocolos de seguridad, previamente establecidos para su cuidado, junto con los conocimientos requeridos para el desarrollo de las técnicas.")])])]),_c('div',{staticClass:"col-lg-7"},[_c('div',{staticClass:"tarjeta tarjeta--blanca p-3"},[_c('img',{attrs:{"src":require("@/assets/curso/imagen-laboratorio-biotecnologia-vegetal.png")}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row tarjeta tarjeta--blanca p-2 p-md-3"},[_c('h3',[_vm._v("Características físicas")]),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-right"}},[_vm._v("Lo invitamos a ver el siguiente video, donde conocerá las características físicas del laboratorio de biotecnología vegetal.")]),_c('figure',{staticClass:"mb-5"},[_c('div',{staticClass:"video"},[_c('iframe',{attrs:{"width":"560","height":"315","src":"https://www.youtube.com/embed/5xTITLjcb2E","title":"YouTube video player","frameborder":"0","allow":"accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture","allowfullscreen":""}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-around align-items-center"},[_c('h3',[_vm._v("Equipamiento y materiales básicos en laboratorios")]),_c('div',{staticClass:"col-lg-5"},[_c('p',{staticClass:"p-4"},[_vm._v("Una vez reconocida la importancia del laboratorio dentro de las prácticas biotecnológicas, es fundamental contar con la instrumentación y las herramientas necesarias para